export default {
	data() {
		return {
			userInfo: {},
			isNext: '2'
		}
	},
	methods: {
		onOrder() {
			uni.navigateTo({
				url: '../../pages/selectmodel/selectmodel'
			});
		},
		onNext() {
			uni.navigateTo({
				url: '../../pages/my/my'
			});
		},
		onGetUserInfo() {
			uni.showLoading({
				title: '正在加载',
				mask: false
			})
			uni.getSetting({
				success: res => {
					if (res.authSetting['scope.userInfo']) {
						console.log('已授权')
						uni.navigateTo({
							url: '../../pages/my/my',
							success:res=>{
								uni.hideLoading()
							}
						})
					}
				}
			})
		}
	}
}
